import React, { useState } from 'react';

function Home() {
  const [string, setString] = useState("");

  const handleInput= (e)=>{
    e.preventDefault()
    setString(e.target.value)
    
    fetch(`https://api.themoviedb.org/3/search/movie?api_key=a3eca5539e3715256e80a664443dbf05&language=en-US&query=${string}&page=1&include_adult=true`)
    .then( data => data.json())
   // .then(console.log(data))
    ;
  }
  return (
    <div>
      <p>search movie</p>
      <input 
        type="text" 
        placeholder="search"
        onChange={handleInput}
        value={string}
      />
    </div>
  )
}

export default Home
